import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import {SvgIcon} from "@material-ui/core";
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

const useStyles = makeStyles(theme => ({
  root: {
    borderLeft: '5px solid transparent',
    borderRight: '5px solid transparent',
    borderTop: '10px solid transparent',
    borderBottom: '10px solid transparent',
  },
  flat: size => ({
    backgroundColor: "#AEC3DC",
    display: 'flex',
  }),
  square: size => ({
    backgroundColor: "#AEC3DC",
    width: size.width,
    height: size.height,
    textAlign: 'center',
    display: 'table-cell',
    verticalAlign: 'middle'
  }),
  icon: size => ({
    color: "#1C4366",
    width: size.imageWidth - 4,
    height: size.imageHeight - 4,
    marginTop: '2px',
    marginBottom: '2px',
    marginLeft: '2px',
    marginRight: '2px'
  }),
  favicon: size => ({
    backgroundColor: "#AEC3DC",
    width: size.imageWidth,
    height: size.imageHeight,
  }),
  textBox: size => ({
    height: size.textBoxHeight,
    marginRight: size.textBoxMarginRight,
  }),
  textTop: size => ({
    color: "#FFFFFF",
    fontSize: size.topFontSize,
  }),
  textBottom: size => ({
    color: "#5592CC",
    fontSize: size.bottomFontSize,
  }),
}));

function HomeIcon(props) {
  return (
    <SvgIcon {...props}>
      <path d="M7.488 24h-.001c-.266 0-.487-.216-.487-.487v-.513c-1.104 0-2-.896-2-2v-13c0-1.104.896-2 2-2h2v-5c0-.552.448-1 1-1h4c.552 0 1 .448 1 1v5h2c1.105 0 2 .896 2 2v13c0 1.104-.895 2-2 2v.488c0 .283-.228.512-.512.512h-.001c-.265 0-.487-.214-.487-.487v-.513h-8v.488c0 .283-.229.512-.512.512zm9.012-13c.277 0 .5-.224.5-.5s-.223-.5-.5-.5h-9c-.276 0-.5.224-.5.5s.224.5.5.5h.507c-.072 1.037.389 1.936.996 2.352l-.003 1.476 3.529 3.543 2.834-2.823-3.529-3.542-1.289-.003c-.124-.364-.335-.721-.578-1.003h6.533zm-4.851 3.271l1.821 1.821-.354.353-1.821-1.82.354-.354zm.683 1.744l-.353.354-1.214-1.214.354-.353 1.213 1.213zm-.153-2.274l1.821 1.821-.354.353-1.82-1.821.353-.353zm-1.572-.726c-.102.283-.331.462-.606.539.042.416.551.6.85.303.279-.276.142-.754-.244-.842zm-1.361-2.015c.334.257.614.638.763 1.002l-1.003-.002-.001.682-.018-.022c-.332-.401-.529-.99-.477-1.66h.736zm4.254-9h-3c-.276 0-.5.224-.5.5v3.5h4v-3.5c0-.276-.224-.5-.5-.5z"/>
    </SvgIcon>
  );
}

export default function Logo(props) {
  var size = {
    width: 52,
    height: 52,
    imageWidth: 52,
    imageHeight: 52,
    textBoxHeight: 52,
    topFontSize: 16,
    bottomFontSize: 20
  };


  if (props.size) {
    size.width = props.size;
    size.height = props.size;
  }
  // square minimum size

  var minimumSize = 64;

  if (props.favicon && props.size > minimumSize) {
    if (props.size <= 72) {
      size.imageWidth = size.width / 9 * 4;
      size.imageHeight = size.height / 9 * 4;
      size.textBoxHeight = parseInt(size.height * 0.3);
    } else {
      size.imageWidth = size.width / 2;
      size.imageHeight = size.height / 2;
      size.textBoxHeight = parseInt(size.height * 0.3);
    }
    size.topFontSize = size.textBoxHeight * 0.4 + 'px';
    size.bottomFontSize = size.textBoxHeight * 0.6 + 'px';
  } else {
    size.imageWidth = size.width;
    size.imageHeight = size.height;
    size.textBoxHeight = parseInt(size.height / 4 * 3);
    size.topFontSize = parseInt(size.textBoxHeight / 10 * 4) + 'px';
    size.bottomFontSize = parseInt(size.textBoxHeight / 10 * 5) + 'px';
    size.textBoxMarginRight = size.imageWidth / 4;
  }

  // console.log(size);

  const classes = useStyles(size);

  return (
    <div className={classes.root}>
    {
      props.favicon ?
        props.size > minimumSize ?
          <div className={classes.square}>
            <HomeIcon className={classes.icon} />
            <Typography className={classes.textBox} component={'span'}>
              <Box className={classes.textTop} fontFamily="Nanum Brush Script">떠나볼까</Box>
              <Box className={classes.textBottom} fontFamily="Nanum Brush Script" fontWeight={700}>스마트컨슈머</Box>
            </Typography>
          </div>
        :
          <div className={classes.favicon}>
            <HomeIcon className={classes.icon} />
          </div>
      :
        <div className={classes.flat}>
          <HomeIcon className={classes.icon} />
          <Typography className={classes.textBox} component={'span'} display='block'>
            <Box className={classes.textTop} fontFamily="Nanum Brush Script">떠나볼까</Box>
            <Box className={classes.textBottom} fontFamily="Nanum Brush Script" fontWeight={700}>스마트컨슈머</Box>
          </Typography>
        </div>
    }
    </div>
  );
}
