import React, { Component } from 'react';
import {Switch, Route, BrowserRouter} from 'react-router-dom'
import App from './App';
import IconViewer from './IconViewer'
import {CookiesProvider, withCookies} from 'react-cookie';
import {MuiThemeProvider} from "@material-ui/core/styles";
import Consts from "./consts";

class TopApp extends Component {
  render() {
    return (
      <MuiThemeProvider theme = { Consts.THEME }>
        <CookiesProvider>
          <BrowserRouter>
            <Switch>
              <Route path={process.env.PUBLIC_URL + "/:menu"} render={props => (<App {...props} cookies={this.props.cookies}/>)}/>
              <Route path={process.env.PUBLIC_URL + "/"} render={props => (<App {...props} cookies={this.props.cookies}/>)}/>
            </Switch>
          </BrowserRouter>
        </CookiesProvider>
      </MuiThemeProvider>
    );
  }
}

export default withCookies(TopApp);
